import {NEW_MESSAGE, SEND_MESSAGE} from "./action";


let initialState = {
    messages: [
        {id: 1, message: 'Hi'},
        {id: 2, message: 'GG,WP'},
        {id: 3, message: 'How are you'},
        {id: 4, message: 'U a crazy'},
        {id: 5, message: 'Whats up man?'},
    ],
    dialogs: [
        {
            id: '1',
            name: 'Jack',
            img: 'data:image/jpeg;base64,'
        },
        {
            id: '2',
            name: 'Helen',
            img: 'data:image/jpeg;base64,'
        },
        {
            id: '3',
            name: 'Olga',
            img: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSs035xXqL_RoZ_UeOppAqf6u9JyGWJ1MtVwA&usqp=CAU'
        },
        {
            id: '4',
            name: 'Yacubovich',
            img: 'data:image/jpeg;base64,'
        },
        {
            id: '5',
            name: 'Obivan K',
            img: 'data:image/jpeg;base64,'
        },
    ],
    newMessageText: ''
};


const dialogsReducer = (state = initialState, action) => {
    switch (action.type) {
        case SEND_MESSAGE:
            return {
                ...state,
                newMessageText: '',
                messages: [...state.messages,
                    {id: 6, message: state.newMessageText}
                ]
            };
        case NEW_MESSAGE:
            return {
                ...state,
                newMessageText: action.newMessageBody
            };
        default:
            return state;
    }
};

export default dialogsReducer;